export class JobInformationReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'JobInformationReport';
		this.viewId = 'JobInformationReport';
		this.context_menu_name = $.i18n._( 'Job Information' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'JobInformationReportView.html';
		this.api = TTAPI.APIJobInformationReport;
	}

	getCustomContextMenuModel() {
		return { include: ['default'] };
	}

}